

















import lausirCodeDialog from '../../components/lausir-codedialog/lausir-codedialog.vue';

export default {
	components: {
		lausirCodeDialog,
	},
	data() {
		return {
			showCodeDialog: false,
			mobile: '',
			verifyCode: ''
		};
	},
	onLoad() {
		uni.setNavigationBarTitle({
			title: '重置密码'
		});
	},
	methods: {
		getReVerifyCode: function() {
			this.showCodeDialog = true;
			var _this = this;
			uni.request({
				url: this.apiServer + '/user/reverify',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					mobile: _this.mobile
				},
				success: res => {
					if (res.data.code === 0) {
						uni.showToast({
							title: '验证码已发送'
						});
						_this.disabled = true;
						console.log(_this.disabled);
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg
						});
					}
				}
			});
		},
		checkCode: function() {
			var _this = this;
			console.log(_this.verifyCode);
			uni.request({
				url: this.apiServer + '/user/check',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					mobile: _this.mobile,
					verifyCode: _this.verifyCode
				},
				success: res => {
					console.log(res.data);
					if (res.data.code === 0) {
						uni.navigateTo({
							url: '../resetpsd/resetpassword?mobile=' + _this.mobile
						});
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg
						});
					}
				}
			});
		},

		change: function(res) {
			if (res.type == 1) {
				this.verifyCode = res.code;
				this.showCodeDialog = false;
			} else if (res.type == -1) {
				this.verifyCode = "请输入验证码";
				this.showCodeDialog = false;
			} else {
				setTimeout(function() {
					res.resendCall()
				}, 3000)
			}


		}
	}

};
